const app = getApp()

export default {
	data() {
		return {
			loadinginfo: true,
			userInfo: {},
			hasUserInfo: false,
			hasTsinghuaInfo: false,
			tsinghuaid: '2017010000',
			canIUse: uni.canIUse('button.open-type.getUserInfo'),
			like: 123,
			follow: 10,
			history: 23,
			current: 0,
			tabs: [
				"活动日程", "报名中"
			],
			userActivity: [{
				end_time: "2019-12-02 19:00:00",
				event_id: "7fb985ec8326b57e24611777e91bc8f8",
				sponsor_name: "123456",
				start_time: "2019-12-01 01:00:00"
			}],
			
			activity: {
				event_name: '活动名',
				event_location: '活动地点',
				like: true,
				end_time: "2019-12-02 19:00:00",
				event_id: "7fb985ec8326b57e24611777e91bc8f8",
				sponsor_name: "123456",
				start_time: "2019-12-01 01:00:00"
			},
			sponsor: {
				avatarUrl: '',
				name: 'xx学生会'
			}
		};
	},
	onLoad() {

		if (app.globalData.userInfo) {
			this.userInfo = app.globalData.userInfo
			console.log(this.userInfo)
			this.hasUserInfo = true
		} else if (this.canIUse) {
			// 由于 getUserInfo 是网络请求，可能会在 Page.onLoad 之后才返回
			// 所以此处加入 callback 以防止这种情况
			app.userInfoReadyCallback = res => {
				this.userInfo = res.userInfo
				console.log(this.userInfo)
				this.hasUserInfo = true
			}
		} else {
			// 在没有 open-type=getUserInfo 版本的兼容处理
			uni.getUserInfo({
				success: res => {
					app.globalData.userInfo = res.userInfo
					this.userInfo = res.userInfo
					console.log(this.userInfo)
					this.hasUserInfo = true
				}
			})
		}

		if (app.globalData.tsinghuaid) {
			this.hasTsinghuaInfo = true
			this.tsinghuaid = app.globalData.tsinghuaid
		}
		uni.showShareMenu({})
	},
	onShow() {
		uni.request({
			url: app.globalData.apiurl + 'users/view', //仅为示例，并非真实接口地址。
			header: {
				'content-type': 'application/json', //自定义请求头信息
				'cookie': app.globalData.cookie
			},
			success: (res) => {
				console.log(res.data)
				this.follow = res.data.follow
				this.history = res.data.history
				this.like = res.data.like
			},
			fail: (res) => {
				console.log('viewfail')
			}
		})

		uni.request({
			url: app.globalData.apiurl + 'users/book', //仅为示例，并非真实接口地址。
			header: {
				'content-type': 'application/json', //自定义请求头信息
				'cookie': app.globalData.cookie
			},
			success: (res) => {
				console.log('users/book')
				console.log(res)
				this.userActivity = res.data.events.map( function(e) { e.like = true})//res.data.events
				
			},
			fail: (res) => {
				console.log('viewfail')
			}
		})

		if (app.globalData.token) {
			console.log('token:' + app.globalData.token),
				uni.request({
					url: app.globalData.apiurl + 'users/tsinghuaid', //仅为示例，并非真实接口地址。
					method: 'POST',
					data: {
						token: app.globalData.token
					},
					header: {
						'content-type': 'application/json', //自定义请求头信息
						'cookie': app.globalData.cookie
					},
					success: (res) => {
						console.log('调用绑定')
						console.log(res.data);
						this.hasUserInfo = true
						this.hasTsinghuaInfo = true
						this.tsinghuaid = res.data.tsinghuaid
						app.globalData.tsinghuaid = res.data.tsinghuaid
						app.globalData.token = undefined
					},
					fail: (res) => {
						console.log('绑定失败')
					}
				})
			app.globalData.token = undefined
		}
	},
	onShareAppMessage(res) {
		return {
		    title: app.globalData.sharetitle,
		    path: '/pages/index/index',
			imageUrl: app.globalData.shareimg
		}
	},
	methods: {
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		getUserInfo(e) {
			console.log(e)
			app.globalData.userInfo = e.detail.userInfo
			this.userInfo = e.detail.userInfo
			this.hasUserInfo = true
		},
		tabSelect(e) {
			this.current = e.currentTarget.dataset.id;
		},
		navChange(index) {
			this.current = index;
		},
		swiperChange(e) {
			this.current = e.detail.current;
		},
		likePage() {
			console.log("likepage")
			uni.navigateTo({
				url: "../like/like"
			})
		},
		followPage() {
			console.log("followpage")
			uni.navigateTo({
				url: "../following/following"
			})
		},
		historyPage() {
			console.log("historypage")
			uni.navigateTo({
				url: "../history/history"
			})
		},
		sponsorPage(name) {
			uni.navigateTo({
				url: "../sponsor/sponsor?id=" + name
			})
		},
		activityPage(id) {
			uni.navigateTo({
				url: "../activity/activity?id=" + id
			})
		},
		announcementPage(id) {

		},
		QRcodePage(id) {

		},
		cancelActivity(index) {
			uni.request({
				url: app.globalData.apiurl + 'users/book/' + this.userActivity[index].event_id, //仅为示例，并非真实接口地址。
				method: 'DELETE',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(res);
					this.userActivity.splice(index, 1)
				},
				fail: (res) => {
					
				}
			})
		},
		likeActivity(index) {
			uni.request({
				url: app.globalData.apiurl + 'users/like/' + id,
				method: 'POST',
				header: {
					'content-type': 'application/json', //自定义请求头信息
					'cookie': app.globalData.cookie
				},
				success: (res) => {
					console.log(index)
					console.log(id)
					this.userActivity[index].like = res.data.like
				}
			});
		},
		identification() {
			console.log(123)
			uni.navigateToMiniProgram({
				appId: "wx1ebe3b2266f4afe0",
				path: "pages/index/index",
				envVersion: "trial",
				extraData: {
					origin: "miniapp",
					type: "id.tsinghua"
				},
				success(res) {
					// 打开成功
					console.log(res)
				},
				fail(res) {
					console.log(res)
				}
			})
		}
	}
}
